@import url("https://fonts.googleapis.com/css2?family=Work+Sans&display=swap");
* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

body {
  font-family: "Work Sans", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}

.static-about {
  position: fixed;
  margin: 20px;
  cursor: pointer;
  z-index: 5;
  color: #f2d0a9;
  top: 0%;
  left: 0%;
}
.static-about a {
  font-size: 3.25vw;
  font-weight: 700;
}

.rotate-about {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: bottom left;
  transform: rotate(90deg);
}

.rotate-back-about {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  color: #f2d0a9;
  transform-origin: bottom left;
  transform: rotate(0deg);
}

.static-projects {
  position: fixed;
  margin: 20px;
  cursor: pointer;
  z-index: 5;
  color: #99c1b9;
  top: 0%;
  right: 0%;
}
.static-projects a {
  font-size: 3.25vw;
  font-weight: 700;
}

.rotate-projects {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: bottom right;
  transform: rotate(-90deg);
}

.rotate-back-projects {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  color: #99c1b9;
  transform-origin: bottom right;
  transform: rotate(0deg);
}

.static-skills {
  position: fixed;
  margin: 20px;
  cursor: pointer;
  z-index: 5;
  color: #d88c9a;
  bottom: 0%;
  left: 0%;
}
.static-skills a {
  font-size: 3.25vw;
  font-weight: 700;
}

.rotate-skills {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: top left;
  transform: rotate(-90deg);
}

.rotate-back-skills {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  color: #d88c9a;
  transform-origin: top left;
  transform: rotate(0deg);
}

.static-contacts {
  position: fixed;
  margin: 20px;
  cursor: pointer;
  z-index: 5;
  color: #8e7dbe;
  bottom: 0%;
  right: 0%;
}
.static-contacts a {
  font-size: 3.25vw;
  font-weight: 700;
}

.rotate-contacts {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: top right;
  transform: rotate(90deg);
}

.rotate-back-contacts {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: top right;
  color: #8e7dbe;
  transform: rotate(0deg);
}

#content {
  margin-top: 20vh;
  max-width: 840px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  color: #f1e3d3;
}

#about-me-wrapper {
  padding: 100px 0;
  font-size: 2.5rem;
  line-height: 3.5rem;
  text-align: center;
  color: #302e2e;
}

#projects-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#project-items {
  max-width: 932px;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  overflow: hidden;
}

.project-item {
  background-color: transparent;
  width: 400px;
  height: 400px;
  margin: 50px 5px;
  perspective: 1000px;
  cursor: pointer;
}

.project-item-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: all 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.project-item:hover .project-item-inner {
  transform: rotateY(180deg);
}

.project-item-front,
.project-item-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.project-item-front {
  outline: 4px solid #f1e3d3;
  outline-offset: -4px; }

.project-item-back {
  background-color: #f1e3d3;
  color: #302e2e;
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#skills-page-wrapper {
  max-width: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #302e2e;
}

.skills-bar {
  margin: 25px 0px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  flex-wrap: wrap;
}

.skill-wrapper {
  height: 120px;
  width: 120px;
  margin: 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e7e7e7;
  border: 2px solid grey;
  border-radius: 4px;
  color: grey;
  -webkit-transition: all 0.2s linear;
  -moz-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.skill-wrapper p {
  font-size: 1.25rem;
}

.skill-wrapper:hover {
  color: black;
  transform: scale(1.3);
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

#contacts-page {
  padding: 100px 0;
  font-size: 2rem;
  line-height: 3.5rem;
  color: #f1e3d3;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#contacts-page hr {
  width: 50%;
  height: 4px;
  color: #f1e3d3;
  background-color: #f1e3d3;
}

#social-media-bar {
  width: 60%;
  height: 60px;
  background-color: #8e7dbe;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.social-media {
  width: 50px;
  height: 50px;
  position: relative;
}
.social-media i {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 32px;
  color: #f1e3d3;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
  background-color: transparent;
  z-index: 2;
}

.social-media:hover {
  cursor: pointer;
}

.spinning-social-media {
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: 5px solid #8e7dbe;
  border-left-color: #f1e3d3;
  border-right-color: #f1e3d3;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  z-index: 999;
}

.rotate-social-media {
  transform: rotate(180deg);
}
